import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigRetencaoFonte } from '../../../../models/dtos/mlf/configRetencaoFonte';
import { DIAS_ENUM_VALUES } from '../../../../models/enumerated/ger/diasEnum';
import { PERIODO_APURACAO_RETENCAO_FONTE_VALUES } from '../../../../models/enumerated/mlf/periodoApuracaoEnum';

interface ConfigRetencaoFonteProps extends EditViewProps<ConfigRetencaoFonte> {}

const ConfigRetencaoFonteEditView: React.FC<ConfigRetencaoFonteProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ConfigRetencaoFonte>({ defaultValues: selectedItem, mode: 'onBlur' });

  const validateExtras = formValues => {
    const erros = [];
    if (formValues.notificarVencimento === true && !formValues.notificarVencimentoDias) {
      erros.push('Dias de Antecedência do vencimento - Campo não pode estar em branco.');
    }
    return erros;
  };

  const calcularAliquotaTotal = () => {
    const { aliquotaPIS, aliquotaCOFINS, aliquotaCSLL, aliquotaIRRF } = formProps.getValues();
    formProps.setValue('aliquotaTotal', Number(aliquotaPIS) + Number(aliquotaCOFINS) + Number(aliquotaCSLL) + Number(aliquotaIRRF));
  };

  const esconderBotaoSubmit = () => {
    const padrao = formProps.getValues().padrao || false;
    return padrao.toString() === 'true';
  };

  return (
    <WindowComponent title="Configurações de Retenção Fonte">
      <EditForm
        formProps={formProps}
        editViewProps={props}
        dtoClassName={CLASS_NAME}
        validateExtras={validateExtras}
        hideSubmitButton={esconderBotaoSubmit()}
      >
        <InputGroupInline formProps={formProps} id="padrao" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 50 } }}
              isPrependLabel={true}
              placeholder="Informe a descrição da Configuração de Retenção Fonte"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="codigoReceita"
              label="Código da Receita"
              formProps={{ ...formProps, validation: { required: true, maxLength: 5 } }}
              isPrependLabel={true}
              placeholder="Informe o código da receita"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="variacaoCodigoReceita"
              label="Variação"
              formProps={{ ...formProps, validation: onlyNumber(0, 2) }}
              inputProps={{ title: 'Variação do código da receita. Esse campo é obrigatório caso a empresa gere o DCTF.' }}
              isPrependLabel={true}
              placeholder="Informe a variação da Receita"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="periodoApuracao"
              label="Período de Apuração"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o período de apuração"
              inputDropDownProps={{ optionsProvider: PERIODO_APURACAO_RETENCAO_FONTE_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaPIS"
              label="Alíquota PIS"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota PIS"
              onBlur={calcularAliquotaTotal}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaCOFINS"
              label="Alíquota COFINS"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota COFINS"
              onBlur={calcularAliquotaTotal}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaCSLL"
              label="Alíquota CSLL"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota CSLL"
              onBlur={calcularAliquotaTotal}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaIRRF"
              label="Alíquota IRRF"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota IRRF"
              onBlur={calcularAliquotaTotal}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaTotal"
              label="Alíquota Total"
              formProps={{ ...formProps, validation: { minValue: 0, maxValue: 100 } }}
              disabled={true}
              isPrependLabel={true}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="diaVencimento"
              label="Dia de Vencimento"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o dia de vencimento"
              inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px' }}>
              <CardBody>
                <CardTitle>Configuração de Notificação</CardTitle>
                <Row>
                  <Col>
                    <CustomInput
                      id="checkNotificarVencimento"
                      type="switch"
                      label="Notificar Vencimento de Imposto"
                      name="notificarVencimento"
                      innerRef={formProps.register}
                      style={{ marginBottom: '16px' }}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="notificarVencimentoDias"
                      label="Dias de Antecedência do vencimento"
                      formProps={formProps}
                      isPrependLabel={true}
                      disabled={!formProps.getValues().notificarVencimento}
                      placeholder="Informe com quantos de dias de antecedência gostaria de ser notificado"
                      inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES, enabled: false }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CustomInput
                      id="checkNotificarVencimentoPorEmail"
                      type="switch"
                      disabled={!formProps.getValues().notificarVencimento}
                      label="Notificar o cliente/responsável por Connect/email, sobre o vencimento do Imposto"
                      name="notificarVencimentoPorEmail"
                      innerRef={formProps.register}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="recolhimentoMinimo"
              label="Recolhimento Mínimo"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o valor do recolhimento mínimo"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="vrLimiteDispensa"
              label="Valor limite de dispensa"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o valor do limite de dispensa"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigRetencaoFonteEditView;
